// Fix for fileuploadser.tsx
import React, { useState, useRef } from 'react';
import FilePreview from './filepreview';

// GraphQL imports (adjust based on your setup)
import { useMutation } from '@apollo/client';
import { GET_PRESIGNED_URL, COMPLETE_FILE_UPLOAD } from '../graphql/mutations';

interface FileUploaderProps {
  onFileUploaded?: (fileUrl: string) => void;
  allowedTypes?: string; // e.g. "image/*,application/pdf"
  maxSizeMB?: number;
}

const FileUploader: React.FC<FileUploaderProps> = ({ 
  onFileUploaded, 
  allowedTypes = "image/*,video/*,application/pdf", 
  maxSizeMB = 10 
}) => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploadError, setUploadError] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // GraphQL mutations
  const [getPresignedUrl] = useMutation(GET_PRESIGNED_URL);
  const [completeFileUpload] = useMutation(COMPLETE_FILE_UPLOAD);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setUploadError(null);
    
    const files = event.target.files;
    if (!files || files.length === 0) {
      setSelectedFile(null);
      return;
    }
    
    const file = files[0];
    
    // Validate file size
    if (file.size > maxSizeMB * 1024 * 1024) {
      setUploadError(`File size exceeds the maximum allowed size of ${maxSizeMB}MB`);
      return;
    }
    
    // Store the file for upload and preview
    setSelectedFile(file);
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      setUploadError('No file selected');
      return;
    }
    
    try {
      setIsUploading(true);
      setUploadProgress(0);
      setUploadError(null);
      
      // Step 1: Get presigned URL from your GraphQL API
      const presignedUrlResult = await getPresignedUrl({
        variables: {
          filename: selectedFile.name,
          contentType: selectedFile.type
        }
      });
      
      const { uploadUrl, fileUrl, key } = presignedUrlResult.data.getPresignedUrl;
      
      // Step 2: Upload the file to S3 using the presigned URL
      const xhr = new XMLHttpRequest();
      
      // Track upload progress
      xhr.upload.addEventListener('progress', (event) => {
        if (event.lengthComputable) {
          const percentComplete = Math.round((event.loaded / event.total) * 100);
          setUploadProgress(percentComplete);
        }
      });
      
      // Handle errors
      xhr.addEventListener('error', () => {
        setUploadError('Upload failed. Please try again.');
        setIsUploading(false);
      });
      
      // Handle abort
      xhr.addEventListener('abort', () => {
        setUploadError('Upload aborted.');
        setIsUploading(false);
      });
      
      // Handle successful upload
      xhr.addEventListener('load', async () => {
        if (xhr.status >= 200 && xhr.status < 300) {
          try {
            // Step 3: Complete the upload by notifying your API
            await completeFileUpload({
              variables: {
                key,
                originalFilename: selectedFile.name,
                fileSize: selectedFile.size,
                fileType: selectedFile.type,
                fileUrl
              }
            });
            
            // Call the callback with the uploaded file URL
            if (onFileUploaded) {
              onFileUploaded(fileUrl);
            }
            
            // Reset the file input
            if (fileInputRef.current) {
              fileInputRef.current.value = '';
            }
            
            // Reset state
            setSelectedFile(null);
            setUploadProgress(0);
          } catch (error) {
            console.error('Error completing the upload:', error);
            setUploadError('Upload succeeded but failed to record the upload. Please contact support.');
          }
        } else {
          setUploadError(`Upload failed with status ${xhr.status}`);
        }
        setIsUploading(false);
      });
      
      // Open the request
      xhr.open('PUT', uploadUrl);
      
      // Set the appropriate content type for the file
      xhr.setRequestHeader('Content-Type', selectedFile.type);
      
      // Send the file
      xhr.send(selectedFile);
      
    } catch (error) {
      console.error('Error initiating upload:', error);
      setUploadError('Failed to initiate upload. Please try again.');
      setIsUploading(false);
    }
  };

  const handleRemoveFile = () => {
    setSelectedFile(null);
    setUploadError(null);
    setUploadProgress(0);
    
    // Reset the file input
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <div className="file-uploader">
      <div className="file-input-container">
        <input
          ref={fileInputRef}
          type="file"
          onChange={handleFileChange}
          accept={allowedTypes}
          disabled={isUploading}
        />
        <button 
          type="button" 
          onClick={() => fileInputRef.current?.click()}
          className="select-file-btn"
          disabled={isUploading}
        >
          Select File
        </button>
      </div>
      
      {uploadError && (
        <div className="upload-error">
          {uploadError}
        </div>
      )}
      
      {selectedFile && (
        <div className="preview-container">
          <FilePreview file={selectedFile} />
          
          <div className="file-actions">
            <button
              type="button"
              onClick={handleUpload}
              disabled={isUploading || !!uploadError}
              className="upload-btn"
            >
              {isUploading ? 'Uploading...' : 'Upload'}
            </button>
            
            <button
              type="button"
              onClick={handleRemoveFile}
              disabled={isUploading}
              className="remove-btn"
            >
              Remove
            </button>
          </div>
          
          {isUploading && (
            <div className="progress-container">
              <div 
                className="progress-bar" 
                style={{ width: `${uploadProgress}%` }}
              ></div>
              <span className="progress-text">{uploadProgress}%</span>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default FileUploader;