// Fix for filepreview.tsx
import React, { useEffect, useState } from 'react';

// Type definitions for props
interface FilePreviewProps {
  file: File | null;
  // Add any other props your component needs
}

const FilePreview: React.FC<FilePreviewProps> = ({ file }) => {
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);

  useEffect(() => {
    // Clean up the previous blob URL when component unmounts or file changes
    return () => {
      if (previewUrl) {
        URL.revokeObjectURL(previewUrl);
      }
    };
  }, [previewUrl]);

  useEffect(() => {
    // Create a new blob URL only when we have a valid file
    if (file) {
      const objectUrl = URL.createObjectURL(file);
      setPreviewUrl(objectUrl);
      
      // Clean up function to revoke the object URL when no longer needed
      return () => {
        URL.revokeObjectURL(objectUrl);
      };
    } else {
      // Reset the preview URL when file is null
      setPreviewUrl(null);
    }
  }, [file]);

  // Render preview based on file type
  if (!file || !previewUrl) {
    return <div className="no-preview">No file selected</div>;
  }

  // Handle different file types
  if (file.type.startsWith('image/')) {
    return (
      <div className="image-preview">
        <img 
          src={previewUrl} 
          alt="File preview" 
          onError={(e) => {
            console.error('Error loading image preview:', e);
            // Set a fallback image or message
            e.currentTarget.src = '/placeholder-image.png';
          }}
        />
      </div>
    );
  }
  
  if (file.type.startsWith('video/')) {
    return (
      <div className="video-preview">
        <video 
          controls 
          src={previewUrl}
          onError={(e) => console.error('Error loading video preview:', e)}
        >
          Your browser does not support video previews.
        </video>
      </div>
    );
  }
  
  // For other file types, just show the file name and type
  return (
    <div className="generic-preview">
      <div className="file-icon">{file.type.split('/')[0]}</div>
      <div className="file-info">
        <p>{file.name}</p>
        <p className="file-size">{(file.size / 1024).toFixed(2)} KB</p>
      </div>
    </div>
  );
};

export default FilePreview;